
.application-sidebar .sidebar-enter-active,
.application-sidebar .sidebar-leave-active {
  transition: all ease .4s;
  -webkit-transition: all ease .4s;
}
.application-sidebar .sidebar-enter,
.application-sidebar .sidebar-leave-active {
  opacity: 0;
}
.application-sidebar .sidebar-container {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  color: #9a9a9a;
  z-index: 501;
}
.application-sidebar .sidebar-container a {
  color: #9a9a9a;
  font-size: 14px;
}
.application-sidebar .sidebar-container .sidebar-overlay {
  background: rgba(0, 0, 0, 0.5);
  position: absolute;
  top: 50px;
  right: 0;
  bottom: 0;
  left: 0;
}
.application-sidebar .sidebar-container .sidebar-overlay .leftNav-enter-active,
.application-sidebar .sidebar-container .sidebar-overlay .leftNav-leave--active {
  transition: right ease .4s;
  -webkit-transition: right ease .4s;
}
.application-sidebar .sidebar-container .sidebar-overlay .leftNav-enter,
.application-sidebar .sidebar-container .sidebar-overlay .leftNav-leave--active {
  right: 380px;
}
.application-sidebar .sidebar-container .sidebar-overlay nav {
  border-top: 1px solid #222;
  box-shadow: 0 1px 1px #363636 inset;
  background: #282828;
  position: absolute;
  display: block;
  top: 0;
  right: 110px;
  bottom: 0;
  left: 0;
}
.application-sidebar .sidebar-container .sidebar-overlay nav li {
  overflow: hidden;
  line-height: 50px;
  height: 51px;
}
.application-sidebar .sidebar-container .sidebar-overlay nav li a {
  display: block;
  /*width: 100%;*/
  padding: 0 16px;
  border-bottom: 1px dotted #333;
}
.application-sidebar .sidebar-container .sidebar-overlay nav li a .right {
  float: right;
  color: #666;
  font-size: 14px;
}



